li .nav-item {
  color: var(--white);
  width: max-content;
  padding-bottom: .2em;
  line-height: 1;
  text-decoration: none;
  position: relative;
}

li .nav-item span {
  white-space: nowrap;
  transform-origin: center;
  transform-style: preserve-3d;
  transition: all .35s ease-out;
  display: inline-block;
}

li .nav-item .primary {
  position: relative;
}

li .nav-item .secondary {
  position: absolute;
  left: 0;
  transform: translateY(.5em)rotateX(90deg);
}

li .nav-item:before {
  content: "";
  width: 100%;
  height: .075em;
  background-color: var(--pikachu);
  transform-origin: 0;
  transition: all .35s ease-out;
  position: absolute;
  bottom: 0;
  transform: scaleX(0);
}

li .nav-item:hover .primary {
  transform: translateY(-.5em)rotateX(90deg);
}

li .nav-item:hover .secondary {
  transform: translateY(0);
}

li .nav-item:hover:before {
  transition-delay: .2s;
  transform: scaleX(1);
}

